        flex-wrap: wrap;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    text-align: center;
  }
}

.App-option {
  width: 100%;
  white-space: nowrap;
  word-break: keep-all;
  text-align: left;
}
.App-option:hover {
  background: #f5f5f5 !important;
  color: black !important;
}

.bottom {
  bottom: 50px;
  position: absolute;
}

.faded {
  color: #ccc;
  margin-top: 30px;
  font-size: 1.2rem;
}



